<template>
    <div>
        <transition name="fade">
        <div class="modal"  v-if="isShowModal">
        
            <div class="backdrop" @click="closeModal()"></div>
            
                <div class="article">
                    <div class="arrow1"><img src="@/assets/icons/arrow.svg" height="70" ></div>
                    <div class="arrow2"><img src="@/assets/icons/arrow.svg" height="70" ></div>
                    <div class="arrow3"><img src="@/assets/icons/arrow.svg" height="70" ></div>
                    <div class="arrow4"><img src="@/assets/icons/arrow.svg" height="70" ></div>
                    <div class="article__img_container">
                        <img :src="$Helper.getImageSrc(selectedProduct,'front')" height="400" alt="" v-if="selectedProduct.images.length > 0">
                        <img src="@/assets/icons/product_none.png" height="400" v-else>
                        
                    </div>
                    <div class="article__data">
                        <div class="top">
                            <img src="@/assets/icons/heart.svg" style="width: 100px; height: 100px">
                            <div class="article__data-container">
                                <span>{{selectedProduct.category}}</span>
                                <p>{{selectedProduct.name}}</p>
                            </div>
                        </div>
                        <div class="bottom">
                            <div class="btns">
                                <button :class="(selectedSizeId == size.id) ? 'btn active' : 'btn'" v-for="size of selectedProduct.sizes" :key="size.id" :disabled="size.amount < 1" @click="selectSize(size.id)">{{size.sizename}}</button>
                            </div>
                            <h4 class="price">{{selectedProduct.price}} руб</h4>
                            <button @click="addCart()" v-if="showAddBtn && selectedSizeId != -1" class="cart-btn">Добавить в корзину <img src="@/assets/icons/cart_black.svg"></button>
                            <button v-if="showAddBtn && selectedSizeId == -1" disabled class="cart-btn">Выберите размер <img src="@/assets/icons/cart.svg"></button>
                            <button class="cart-btn added" v-if="!showAddBtn" @click="$router.push('/cart')">В корзине <img src="@/assets/icons/cart.svg"></button>
                            
                        </div>
                    </div>
                </div>
        </div>
        </transition>
        
        <HeaderComp></HeaderComp>
        <div class="wrapper">
            <div class="filters">
                <div class="filter">
                    <span class="filter-name">ПОЛ</span>
                    <div class="filter-values">
                        <span class="filter-value" @click="filterGender(1)">мужской</span>
                        <span class="filter-value" @click="filterGender(0)">женский</span>
                        <span class="filter-value"  @click="filterGender(2)">унисекс</span>
                    </div>
                </div>
                <div class="filter">
                    <span class="filter-name">Категория</span>
                    <div class="filter-values">
                        <span class="filter-value" @click="filterCategory(cat.id)" v-for="cat in cats" :key="cat.id">{{ cat.name }}</span>
                    </div>
                </div>
                <div class="filter">
                    <span class="filter-name">Размер</span>
                    <div class="filter-values">
                        <span class="filter-value" v-for="size in sizeMap" @click="filterSize(size)" :key="size">{{size}}</span>
                    </div>
                </div>
            </div>
            <div class="products-view">
                <div class="filters-applied">
                    <div class="filter-applied" style="opacity: 0;" v-if="Object.keys(filters).length < 1">
                        <span class="filter-applied-name">empty</span>
                    </div>
                    <div class="filter-applied"  v-for="filter in textFilters.split(';')" :key="filter" >
                        <span class="filter-applied-name" v-if="filter.split('=')[0].length > 0">{{ filter.split('=')[1] }}<span class="remove-filter" @click="removeFilter(filter.split('=')[0])">x</span></span>
                    </div>
                </div>
                
                <div class="products">
                    <div class="element"  @click="showModal(product)" v-for="product of products" :key="product.id">
                        <i><div class="favorite"></div></i>
                        <div class="image-container" :style="`--front-image: url(${$Helper.getImageSrc(product,'front')}); --back-image:url(${$Helper.getImageSrc(product,'back')})`"  v-if="product.images.length > 1"></div>
                        <img src="@/assets/icons/product_none.svg" height="300" v-if="product.images.length < 1"  alt="">
                        <div class="product-meta">
                            <span>{{ product["name"] }}</span>
                            <span>{{ product["price"] }} руб.</span>
                        </div>
                    </div>
                </div>
           
            </div>
           
        </div>
        
    </div>
</template>
<script>
import HeaderComp from './HeaderComp.vue';

    
    export default {
        name : "catalog-comp",
        components : {
            HeaderComp
        },
        data(){
            return {
                selectedProduct : false,
                selectedSizeId : -1,
                selectedProductCartElement : false,
                products : [],
                sizeMap : ["S","M","L","XL","XXL"],
                isShowModal : false,
                showAddBtn : false,
                cats : [],
                filters : {},
                textFilters : ""
            }
        },
        mounted(){
            this.getProds();
            this.$CoreAPI.getCategories().then((data)=>{
                this.cats = data.categories;
            })
        },
        methods: {
            getProds(){


                this.textFilters = "";
                let filterQuery = ``;
                function getCatName(id,cats){
                    for(let i of cats){
                        if(i.id == id) return i.name
                    }
                }

                function getGenderName(g){
                    if(g == 0) return 'Женский';
                    if(g == 1) return 'Мужской';
                    if(g == 2) return 'Унисекс';
                }
                console.log(this.filters);
                if(Object.keys(this.filters).length > 0){
                    filterQuery = `&filter=enabled`;
                    this.textFilters = "";
                    for(let filterName in this.filters){
                        filterQuery += `&${filterName}=${this.filters[filterName]}`;
                        if(filterName == "category"){
                            this.textFilters += `category=${getCatName(this.filters[filterName],this.cats)};`
                        }
                        if(filterName == "gender"){
                            this.textFilters += `gender=${getGenderName(this.filters[filterName])};`
                        }
                        if(filterName == "size"){
                            this.textFilters += `size=${this.filters[filterName]};`
                        }
                        
                    }
                    console.log(filterQuery);
                }

                this.$CoreAPI.getProducts(filterQuery).then((data)=>{
                    this.products = data.products;
                })
            },
            selectSize(id){
                this.selectedSizeId = id;
                this.checkCart();
            },
            minusFromCart(){
                this.$CoreAPI.client_updateProdAmount(this.selectedProduct.id,this.selectedSizeId,-1);
                this.checkCart();
            },
            plusToCart(){
                this.$CoreAPI.client_updateProdAmount(this.selectedProduct.id,this.selectedSizeId,1);
                this.checkCart();
            },
            closeModal(){
                this.isShowModal = false;
                this.selectedProduct = false;
                this.showAddBtn = false;
                this.selectedSizeId = -1;
            },
            addCart(){
                let product = this.selectedProduct;
                this.$CoreAPI.client_addToCart(product,this.selectedSizeId);
                this.showAddBtn = false
                this.checkCart();
            },
            checkCart(){
                let product = this.selectedProduct;
                console.log(product);
                if(this.$CoreAPI.client_getFromCart(product.id,this.selectedSizeId)){
                    this.showAddBtn = false
                    this.selectedProductCartElement = this.$CoreAPI.client_getFromCart(product.id,this.selectedSizeId)
                }
                else{
                    this.showAddBtn = true;
                }
            },
            showModal(product){
                this.selectedProduct = product;
                this.isShowModal = true;
                this.checkCart();
            },
            filterCategory(id){
                this.filters.category = id;
                this.getProds();
            },
            filterGender(gender){
                this.filters.gender = gender;
                this.getProds();
            },
            filterSize(size){
                this.filters.size= size;
                this.getProds();
            },
            removeFilter(filter){
                delete this.filters[filter];
                this.getProds();
            }
        },
    }
</script> 
<style lang="scss" scoped>
.image-container{
    height: 300px;
    background-image: var(--front-image);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
}

.products-view{
    margin-left: 2vw;
}
.filters-applied{
    display: flex;
    row-gap: 1vw;
}
.remove-filter{
    margin-left: 20px;
    rotate: 90deg;
    color: #a8a8a8;
    font-weight: normal;
}
.filter-applied-name{
    margin-left: 10px;
    font-weight: bold;
    background: #E4FC00;
    padding: 10px;
    display: flex;
    justify-content: center;
    user-select: none;
    align-items: center;
    
}
.filters{
    display: flex;
    flex-direction: column;
    width: 300px;
    align-items: center;
    position: relative;
}
.filter{
    display: flex;
    flex-direction: column;
    width: 100%;
    user-select: none;
}
.filter-name{
    padding: 10px;
    width: inherit;
    display: block;
    text-transform: uppercase;
    text-align: center;
    border: 1px solid #dadada;
}
.filter-values{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    font-size: 0.8em;
}
.filter-value{
    padding: 5px 10px;
    width: 100%;
    text-align: center;
}
.filter-value:hover{
    background: #E4FC00;
}

button{
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    width: auto;

    & img{
        width: 50px;
        height: 50px;
    }
}

.cart-btn:hover:not(.added):not(:disabled){
    background: #1d1d1d;
}
.arrow1{
    rotate: 45deg;
    position: absolute;
    left: 0px;
    bottom: 0px;
}
.arrow2{
    rotate: 135deg;
    position: absolute;
    left: 0px;
    top: 0px;
}
.arrow3{
    rotate: 225deg;
    position: absolute;
    right: 0px;
    top: 0px;
}
.arrow4{
    rotate: 315deg;
    position: absolute;
    right: 0px;
    bottom: 0px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.backdrop{
    position: absolute;
    z-index: 2;
    height: 100vh;
    display: flex;
    
    width: 100vw;
    background-color: rgba(0,0,0,0.3);
}
.cart-btn{
    padding: 0 10px;
    white-space: nowrap;
    background: #000;
    width: 300px;
    transition: all .3s linear 0s;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.7em;
    color: #fff;
    text-transform: uppercase;
}
.cart-btn:disabled{
    background-color: #b2b2b2;
    color: #888888;
    cursor: not-allowed;
}
.cart-btn.added{
    background-color: #E4FC00 ;
    color: #000
}
.modal{
    position: fixed;
    width: 100%;
    z-index: 3;
    display: flex;
    justify-content: center;
}
.article{
    width: inherit;
    
    margin-top: 15vh;
    background-color: #fff;
    width: 50vw;
    position: absolute;
    display: flex;
    z-index: 3;
    flex-direction: row;
    align-items: center;
    padding: 3rem;
    justify-content: space-between;

    &__img_container{
        height: 85%;
    }
    &__actions{
        display: flex;
        justify-content: space-between;
        width: 200px;
        align-items: center;
    }
    &__change_amount{
        height: 50px;
        width: 50px;
        display: block;
        &:hover{
            background-color: #e7e7e7;
        }
    }
    &__data{
        flex: 1;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;

        & .top{
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 16px;
            font-size: 32px;

            & .article__data-container{
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                padding-bottom: 4px;
                border-bottom: 4px solid #000;
            
                & span{
                    font-weight: bold;
                    font-size: 30px;
                }
            }
        }

        & .bottom{
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 32px;
            font-size: 24px;

            & .btns{
                display: inline-flex;
                align-items: center;
                gap: 16px;

                & .btn{
                    width: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 50px;
                    background-color: transparent;

                    &:hover{
                        background-color: var(--default-yellow-color);
                    }

                    &:disabled{
                        background-color: var(--default-grey-color-25);
                    }
                }
                & .active{
                    background-color: var(--default-yellow-color);
                }
            }

            & .price{
                font-weight: bold;
                font-size: 32px;
            }
        }
    }
}
.element{
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.1);
    width: 200px;
    position: relative;
    display: flex;
    padding: 20px;  
    flex-direction: column;
    cursor: pointer;
    user-select: none;
    margin: 10px;
    transition: 0.3s linear 0s;
    max-height: 250px;
}
.product-meta{
    display: flex;
    align-items: flex-end;
    row-gap: 0.5vh;
    font-weight: bold;
    flex-direction: column;
}
.element:hover{
    box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.1);
}
.element:hover .image-container{
    background-image: var(--back-image);
}
.wrapper{
    display: flex;
}
.products{
    display: flex;
}
.favorite{
    height: 25px;
    width: 25px;
    position: absolute;
    right: 10px;
    top:10px;
    background-image: url("@/assets/icons/heart.svg");
    background-size: contain;
    background-repeat: no-repeat;
}
.favorite:hover{
    background-image: url("@/assets/icons/heart_filled.svg");
    background-repeat: no-repeat;
}
</style> 